.popupModel {
  &.maxWindow {
    :global {
      .ant-modal {
        width: 100% !important;
        height: 100%;
        max-width: 100%;
      }
      .ant-modal-content {
        height: 100%;
        border-radius: 0;
      }
    }
  }
  :global {
    .ant-modal-content {
      padding: 0 !important;
      border-radius: 12px;
      // height: 640px;
      overflow: hidden;
      background-color: transparent;
      .ant-modal-body {
        height: 100%;
      }
    }
  }
  .dialogPanel {
    height: 100%;
    width: 100%;
    overflow: auto;
    position: relative;
  }
  .dialogTool {
    position: absolute;
    top: 8px;
    right: 8px;
    display: flex;
    z-index: 1000;
    &.reverse {
      left: 8px;
      right: initial;
      flex-direction: row-reverse;
    }
    .btn {
      display: flex;
      height: 16px;
      width: 16px;
      border-radius: 50%;
      align-items: center;
      justify-content: center;
      margin: 0 4px;
      cursor: pointer;
      color: #fff;
      font-size: 8px;
      transition: all 0.3s;
      &.yellow {
        background-color: rgb(245, 185, 75);
      }
      &.green {
        background-color: rgb(52,199,89);
      }
      &.red {
        background-color: rgb(255,115,48);
      }
      &.blue {
        background-color: #69b1ff;
      }
      & > i {
        opacity: 0;
        transition: all 0.3s;
      }
      &:hover > i {
        opacity: 1;
      }
    }
  }
}

.iconClose {
  position: relative;
  &::before, &::after {
    content: '';
    display: block;
    height: 3px;
    width: 10px;
    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    top: 50%;
    left: 50%;
  }
  &::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }
  &::before {
    transform: translate(-50%, -50%) rotate(45deg);
  }
}
.iconExpandOut, .iconCompressOut {
  position: relative;
  &::before, &::after {
    content: '';
    display: block;
    height: 0px;
    width: 0px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 2px;
  }
}
.iconExpandOut {
  &::before {
    border-top: 3px solid #fff;
    border-left: 3px solid #fff;
    border-bottom: 3px solid transparent;
    border-right: 3px solid transparent;
    margin: -1px;
  }
  &::after {
    border-top: 3px solid transparent;
    border-left: 3px solid transparent;
    border-bottom: 3px solid #fff;
    border-right: 3px solid #fff;
    margin: 1px;
  }
}
.iconCompressOut {
  &::before {
    border-top: 3px solid transparent;
    border-left: 3px solid transparent;
    border-bottom: 3px solid #fff;
    border-right: 3px solid #fff;
    margin: -3px;
  }
  &::after {
    border-top: 3px solid #fff;
    border-left: 3px solid #fff;
    border-bottom: 3px solid transparent;
    border-right: 3px solid transparent;
    margin: 3px;
  }
}
.iconRetract {
  position: relative;
  &::before {
    content: '';
    display: block;
    height: 3px;
    width: 10px;
    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)
  }
}

.iconLink {
  position: relative;
  &::before, &::after {
    content: '';
    display: block;
    position: absolute;
    width: 3px;
    height: 3px;
    border: 2px solid #fff;
    border-radius: 4px;
    transform: rotate(-15deg);
  }
  &::before {
    left: -5px;
    top: -2px;
  }
  &::after {
    left: -2px;
    top: -5px;
  }
}

.viewResizeContainer {
  position: relative;
  .viewResizePanel {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
}
