.container {
  width: 100%;
  height: 100%;
  font-size: 14px;
  position: relative;
  // color: #fff;

  .panel {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 10px;
  }

  .bgCover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .left {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 44px;
  }

  .right {
    flex: 1;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 10px;
  }

  .icon {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 32px;
    font-weight: 600;
    color: #597ef7;
    filter: grayscale(1);
    transition: filter 0.2s;
  }

  .title {
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 4px;
    line-height: 24px;
  }

  .info {
    font-size: 12px;
  }

  &:hover {
    .icon {
      animation: bounce 1s infinite;
      transform-origin: center;
      filter: grayscale(0);
    }

    @keyframes bounce {
      0%, 20%, 50%, 80% {
        transform: scale3d(1, 1, 1);
      }

      35%, 65% {
        transform: scale3d(1.1, 1.1, 1.1);
      }
    }
  }
}
