.copyright {
  font-size: 12px;
  // color: var(--system-text-color, rgb(44 52 55 / 1));
  color: rgba(255, 255, 255, 0.85);
  text-shadow: 0 0 1px rgba(0, 0, 0, .2);
  line-height: 16px;
  text-align: center;
  padding: 8px 0;
  user-select: none;
  & > div {
    display: none;
    &:nth-child(1),
    &:nth-child(2) {
      display: block;
    }
  }
  &:hover {
    & > div {
      &:nth-child(2) {
        display: none;
      }
      &:nth-child(3) {
        display: block;
      }
    }
  }
}
