.mainContainer {
  height: 100%;
  width: 100%;
  position: relative;
  color: #fff;
  text-shadow: 0 0 .06rem rgba(0, 0, 0, .2);
  .panel {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    flex-direction: column;
  }
  .bgCover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    filter: blur(2px);
  }
}

.miniContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  position: relative;
  color: #fff;
  text-shadow: 0 0 .06rem rgba(0, 0, 0, .2);
  .header {
    height: 48px;
  }
  .floor {
    display: flex;
    flex-flow: wrap;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-direction: column;
  }

  .nowTmp {
    font-size: 80px;
    line-height: 76px;
    height: 76px;
    margin: 32px 0;
  }
  .info {
    text-align: left;
    margin: 32px 0;
    & > div {
      margin: 4px 0;
      line-height: 20px;
      height: 20px;
      font-size: 13px;
      width: 200px;
      span {
        margin-left: 8px;
      }
    }
  }
  .weatherInfo {
    width: 100%;
    display: flex;
    font-size: 16px;
    flex-wrap: wrap;
    align-items: center;
    align-content: space-around;
    margin: -8px 0;
    padding: 32px 0;
    & > div {
      width: 33.33%;
      margin: 8px 0;
      h5 {
        margin-bottom: 4px;
      }
      span {
        font-size: 14px;
      }
    }
  }
}

.mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(42, 56, 27, 0.1), rgba(18, 18, 16, 0.2));
}

.backdrop {
  position: relative;
  height: 100%;
  // .mask {
  //   backdrop-filter: blur(10px);
  // }
}

.bgPanel {
  height: 100%;
  background-color: #201f1e;
  position: relative;
  .image {
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    height: 100%;
    width: 100%;
    background-color: transparent;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  .video {
    position: relative;
    object-fit: cover;
    height: 100%;
    width: 100%;
  }
}

.hoursPanel {
  width: 100%;
  flex: 1;
  margin-bottom: 8px;
  &.listPanel {
    ul li {
      padding: 2px 0;
    }
  }
  .linePanel {
    top: 2px;
    bottom: 42px;
  }
}

.weekPanel {
  width: 100%;
  margin-top: 8px;
  flex: 2;
  .linePanel {
    top: 50px;
    bottom: 30px;
  }
}

.listPanel {
  display: flex;
  flex-direction: column;
  padding: 12px;
  h3 {
    font-size: 13px;
    margin-bottom: 8px;
    color: #fff;
    text-shadow: 0 0 .06rem rgba(0, 0, 0, .2);
  }
  ul {
    list-style: none;
    display: flex;
    flex: 1;
    position: relative;
    li {
      &:first-child {
        background-color: rgba(255, 255, 255,0.08);
        border-left: none;
      }
      &:hover {
        background-color: rgba(255, 255, 255,0.06);
      }
      width: 100%;
      font-size: 12px;
      padding: 10px 0;
      border-left: 1px solid rgba(255, 255, 255, .05);
      // background-color: rgba(0, 0, 0,0.1);
      text-align: center;
      transition: background .2s;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      position: relative;
      z-index: 1;
      border-radius: 4px;
      .text {
        line-height: 20px;
        font-size: 11px;
      }
    }
  }
  .linePanel {
    position: absolute;
    width: 100%;
    z-index: 0;
    & > div {
      width: 100%;
      height: 100%;
    }
  }
}
